import request from '@/utils/request'
import type { AdminMetrics, AdminLog, ContentFlag } from '@/types/admin'
import type { UserDetail } from '@/types/user'
import type { PageResponse } from '@/types/api'

// 管理员指标
export async function getMetrics(): Promise<AdminMetrics> {
  const res = await request.get<AdminMetrics>('/admin/metrics')
  return res.data
}

// 用户列表（管理端）
export async function getUserList(params: {
  q?: string
  role?: string
  status?: string
  page?: number
  size?: number
}): Promise<PageResponse<UserDetail>> {
  const page = params.page ?? 1
  const size = params.size ?? 10
  const res = await request.get<{ list: Record<string, unknown>[]; total: number }>(
    '/admin/users',
    {
      params: { q: params.q, role: params.role, status: params.status, page, size },
    }
  )
  const list = (res.data.list ?? []).map((d) => ({
    id: d.id as number,
    email: d.email as string,
    phone: d.phone as string | undefined,
    nickname: (d.nickname as string) ?? '',
    avatar: d.avatar as string | undefined,
    role: d.role as UserDetail['role'],
    status: d.status as UserDetail['status'],
    created_at: (d.created_at as string) ?? '',
    updated_at: (d.updated_at as string) ?? '',
  }))
  return { items: list, total: res.data.total ?? 0, page, size }
}

// 更新用户（管理端）
export async function updateUser(
  id: number,
  data: { role?: string; status?: string }
): Promise<void> {
  await request.patch(`/admin/users/${id}`, data)
}

// 操作日志
export async function getAdminLogs(params?: {
  page?: number
  size?: number
}): Promise<PageResponse<AdminLog>> {
  const page = params?.page ?? 1
  const size = params?.size ?? 20
  const res = await request.get<{ list: AdminLog[]; total: number }>('/admin/logs', {
    params: { page, size },
  })
  return {
    items: res.data.list ?? [],
    total: res.data.total ?? 0,
    page,
    size,
  }
}

// 内容标记列表
export async function getContentFlags(params?: {
  page?: number
  size?: number
}): Promise<PageResponse<ContentFlag>> {
  const page = params?.page ?? 1
  const size = params?.size ?? 10
  const res = await request.get<{ list: ContentFlag[]; total: number }>('/admin/flags', {
    params: { page, size },
  })
  return {
    items: res.data.list ?? [],
    total: res.data.total ?? 0,
    page,
    size,
  }
}

// 更新内容标记状态
export async function updateContentFlag(id: number, status: 'open' | 'resolved'): Promise<void> {
  await request.patch(`/admin/flags/${id}`, { status })
}
