<template>
  <div class="admin-page">
    <div class="admin-container">
      <el-row :gutter="20">
        <el-col :span="4">
          <el-menu :default-active="activeMenu" class="admin-menu" @select="handleMenuSelect">
            <el-menu-item index="dashboard">
              <el-icon><DataBoard /></el-icon>
              <span>数据看板</span>
            </el-menu-item>
            <el-menu-item index="users">
              <el-icon><User /></el-icon>
              <span>用户管理</span>
            </el-menu-item>
            <el-menu-item index="content">
              <el-icon><Document /></el-icon>
              <span>内容管理</span>
            </el-menu-item>
            <el-menu-item index="logs">
              <el-icon><List /></el-icon>
              <span>操作日志</span>
            </el-menu-item>
          </el-menu>
        </el-col>
        <el-col :span="20">
          <!-- 数据看板 -->
          <div v-show="activeMenu === 'dashboard'" class="dashboard-content">
            <h2 class="header-font">数据看板</h2>
            <el-row :gutter="20" class="metrics-row">
              <el-col :span="6">
                <el-card shadow="hover">
                  <div class="metric-item">
                    <div class="metric-value">{{ metrics.user_count }}</div>
                    <div class="metric-label">总用户数</div>
                  </div>
                </el-card>
              </el-col>
              <el-col :span="6">
                <el-card shadow="hover">
                  <div class="metric-item">
                    <div class="metric-value">{{ metrics.active_user_count }}</div>
                    <div class="metric-label">活跃用户</div>
                  </div>
                </el-card>
              </el-col>
              <el-col :span="6">
                <el-card shadow="hover">
                  <div class="metric-item">
                    <div class="metric-value">{{ metrics.essay_count }}</div>
                    <div class="metric-label">作文提交量</div>
                  </div>
                </el-card>
              </el-col>
              <el-col :span="6">
                <el-card shadow="hover">
                  <div class="metric-item">
                    <div class="metric-value">{{ metrics.token_usage.total }}</div>
                    <div class="metric-label">Token使用总量</div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
            <el-card shadow="never" class="token-card">
              <h3 class="header-font">Token使用情况</h3>
              <el-descriptions :column="3" border>
                <el-descriptions-item label="今日使用">
                  {{ metrics.token_usage.today }}
                </el-descriptions-item>
                <el-descriptions-item label="本月使用">
                  {{ metrics.token_usage.this_month }}
                </el-descriptions-item>
                <el-descriptions-item label="总使用量">
                  {{ metrics.token_usage.total }}
                </el-descriptions-item>
              </el-descriptions>
            </el-card>
            <p class="dashboard-note">活跃用户：统计最近 7 天内有过登录的用户。</p>
          </div>

          <!-- 用户管理 -->
          <div v-show="activeMenu === 'users'" class="users-content">
            <h2 class="header-font">用户管理</h2>
            <el-card shadow="never">
              <div class="search-bar">
                <el-input
                  v-model="userSearchQuery"
                  placeholder="搜索用户..."
                  style="width: 300px"
                  clearable
                  @keyup.enter="fetchUsers"
                >
                  <template #prefix>
                    <el-icon><Search /></el-icon>
                  </template>
                </el-input>
                <el-select
                  v-model="userRoleFilter"
                  placeholder="角色"
                  style="width: 120px"
                  clearable
                >
                  <el-option label="学生" value="student" />
                  <el-option label="管理员" value="admin" />
                </el-select>
                <el-select
                  v-model="userStatusFilter"
                  placeholder="状态"
                  style="width: 120px"
                  clearable
                >
                  <el-option label="正常" value="active" />
                  <el-option label="禁用" value="disabled" />
                </el-select>
                <el-button type="primary" @click="fetchUsers">搜索</el-button>
              </div>
              <el-table
                v-loading="usersLoading"
                :data="users"
                style="width: 100%; margin-top: 20px"
              >
                <el-table-column prop="id" label="ID" width="80" />
                <el-table-column prop="nickname" label="昵称" />
                <el-table-column prop="email" label="邮箱" />
                <el-table-column prop="role" label="角色" width="100">
                  <template #default="{ row }">
                    <el-tag :type="row.role === 'admin' ? 'danger' : 'primary'">
                      {{ row.role === 'admin' ? '管理员' : '学生' }}
                    </el-tag>
                  </template>
                </el-table-column>
                <el-table-column prop="status" label="状态" width="100">
                  <template #default="{ row }">
                    <el-tag :type="row.status === 'active' ? 'success' : 'info'">
                      {{ row.status === 'active' ? '正常' : '禁用' }}
                    </el-tag>
                  </template>
                </el-table-column>
                <el-table-column label="操作" width="200">
                  <template #default="{ row }">
                    <el-button type="primary" text @click="handleEditUser(row)">编辑</el-button>
                    <el-button
                      :type="row.status === 'active' ? 'warning' : 'success'"
                      text
                      @click="handleToggleUserStatus(row)"
                    >
                      {{ row.status === 'active' ? '禁用' : '启用' }}
                    </el-button>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                v-model:current-page="userPage"
                v-model:page-size="userPageSize"
                :total="userTotal"
                layout="total, prev, pager, next"
                style="margin-top: 20px"
                @current-change="fetchUsers"
              />
            </el-card>
          </div>

          <!-- 内容管理 -->
          <div v-show="activeMenu === 'content'" class="content-content">
            <h2 class="header-font">内容管理</h2>
            <el-card shadow="never">
              <el-table v-loading="flagsLoading" :data="flags" style="width: 100%">
                <el-table-column prop="id" label="ID" width="80" />
                <el-table-column prop="target_type" label="类型" width="120" />
                <el-table-column prop="target_id" label="目标ID" width="100" />
                <el-table-column prop="reason" label="原因" />
                <el-table-column prop="status" label="状态" width="100">
                  <template #default="{ row }">
                    <el-tag :type="row.status === 'resolved' ? 'success' : 'warning'">
                      {{ row.status === 'resolved' ? '已处理' : '待处理' }}
                    </el-tag>
                  </template>
                </el-table-column>
                <el-table-column label="操作" width="150">
                  <template #default="{ row }">
                    <el-button
                      v-if="row.status === 'open'"
                      type="success"
                      text
                      @click="handleResolveFlag(row)"
                    >
                      标记已处理
                    </el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-card>
          </div>

          <!-- 操作日志 -->
          <div v-show="activeMenu === 'logs'" class="logs-content">
            <h2 class="header-font">操作日志</h2>
            <el-card shadow="never">
              <el-table v-loading="logsLoading" :data="logs" style="width: 100%">
                <el-table-column prop="id" label="ID" width="80" />
                <el-table-column prop="action" label="操作" />
                <el-table-column prop="target_type" label="目标类型" width="120" />
                <el-table-column prop="target_id" label="目标ID" width="100" />
                <el-table-column prop="ip" label="IP" width="150" />
                <el-table-column prop="created_at" label="时间" width="180">
                  <template #default="{ row }">
                    {{ new Date(row.created_at).toLocaleString() }}
                  </template>
                </el-table-column>
              </el-table>
            </el-card>
          </div>
        </el-col>
      </el-row>
    </div>

    <!-- 编辑用户对话框 -->
    <el-dialog v-model="showUserDialog" title="编辑用户" width="500px">
      <el-form :model="editUserForm" label-width="100px">
        <el-form-item label="角色">
          <el-select v-model="editUserForm.role">
            <el-option label="学生" value="student" />
            <el-option label="管理员" value="admin" />
          </el-select>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="editUserForm.status">
            <el-option label="正常" value="active" />
            <el-option label="禁用" value="disabled" />
          </el-select>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="showUserDialog = false">取消</el-button>
        <el-button type="primary" @click="handleSaveUser">保存</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue'
import * as adminApi from '@/api/admin'
import { ElMessage } from 'element-plus'
import { DataBoard, User, Document, List, Search } from '@element-plus/icons-vue'
import type { AdminMetrics, AdminLog, ContentFlag } from '@/types/admin'
import type { UserDetail } from '@/types/user'

const activeMenu = ref('dashboard')

const metrics = ref<AdminMetrics>({
  user_count: 0,
  active_user_count: 0,
  essay_count: 0,
  token_usage: { total: 0, today: 0, this_month: 0 },
})

const users = ref<UserDetail[]>([])
const usersLoading = ref(false)
const userSearchQuery = ref('')
const userRoleFilter = ref('')
const userStatusFilter = ref('')
const userPage = ref(1)
const userPageSize = ref(10)
const userTotal = ref(0)

const flags = ref<ContentFlag[]>([])
const flagsLoading = ref(false)

const logs = ref<AdminLog[]>([])
const logsLoading = ref(false)

const showUserDialog = ref(false)
const editUserForm = ref({ role: '', status: '' })
const currentEditUserId = ref<number | null>(null)

onMounted(async () => {
  await fetchMetrics()
  await fetchUsers()
  await fetchFlags()
  await fetchLogs()
})

async function fetchMetrics() {
  try {
    metrics.value = await adminApi.getMetrics()
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '获取数据失败')
  }
}

async function fetchUsers() {
  usersLoading.value = true
  try {
    const response = await adminApi.getUserList({
      q: userSearchQuery.value,
      role: userRoleFilter.value,
      status: userStatusFilter.value,
      page: userPage.value,
      size: userPageSize.value,
    })
    users.value = response.items
    userTotal.value = response.total
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '获取用户列表失败')
  } finally {
    usersLoading.value = false
  }
}

async function fetchFlags() {
  flagsLoading.value = true
  try {
    const response = await adminApi.getContentFlags({})
    flags.value = response.items
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '获取内容标记失败')
  } finally {
    flagsLoading.value = false
  }
}

async function fetchLogs() {
  logsLoading.value = true
  try {
    const response = await adminApi.getAdminLogs({})
    logs.value = response.items
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '获取日志失败')
  } finally {
    logsLoading.value = false
  }
}

function handleMenuSelect(key: string) {
  if (key === activeMenu.value) return
  activeMenu.value = key
}

function handleEditUser(user: UserDetail) {
  currentEditUserId.value = user.id
  editUserForm.value = {
    role: user.role,
    status: user.status,
  }
  showUserDialog.value = true
}

async function handleSaveUser() {
  if (!currentEditUserId.value) return
  try {
    await adminApi.updateUser(currentEditUserId.value, editUserForm.value)
    ElMessage.success('更新成功')
    showUserDialog.value = false
    await fetchUsers()
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '更新失败')
  }
}

async function handleToggleUserStatus(user: UserDetail) {
  try {
    await adminApi.updateUser(user.id, {
      status: user.status === 'active' ? 'disabled' : 'active',
    })
    ElMessage.success('操作成功')
    await fetchUsers()
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '操作失败')
  }
}

async function handleResolveFlag(flag: ContentFlag) {
  try {
    await adminApi.updateContentFlag(flag.id, 'resolved')
    ElMessage.success('标记已处理')
    await fetchFlags()
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '操作失败')
  }
}
</script>

